let defaultState = {
  matches: [],
  deliveries: [],
  page: 0 + 1
};

function action(state = defaultState, action) {
  debugger;
  switch (action.type) {
    case "LOAD_MATCHES":
      return {
        ...state,
        matches: action.data.matches,
        totalMatches: action.data.totalMatches
      };
    case "LOAD_DELIVERIES":
      return { ...state, deliveries: action.deliveries };
    case "INC_MATCHES":
      debugger;
      return {
        ...state,
        page: state.page + 1,
        matches: action.matches
      };
    case "DEC_MATCHES":
      debugger;
      return {
        ...state,
        page: state.page - 1,
        matches: action.matches
      };
    default:
      return state;
  }
}

export default action;
